<!-- 컴포넌트 ui 정의 -->
<!-- root element가 하나만 있어야함 -->
<template>
  <v-card class="ma-0">
    <v-card-title class="text-center justify-center py-3">
      <span class="font-weight-bold text-h5 navy--text">랭킹</span>
    </v-card-title>
    <v-container fluid>
      <v-row class="ma-0 pa-0">
        <v-col class="text-left">
          <v-btn-toggle v-model="toggle_exclusive" mandatory borderless>
            <v-btn>월간</v-btn>
            <v-btn>주간</v-btn>
          </v-btn-toggle>
        </v-col>
        <v-col class="text-right">
          <v-btn class="navy white--text" @click="getMyRanking">내 순위 보기</v-btn>
        </v-col>
      </v-row>
      <v-list two-line v-for="person in people" :key="person.name" class="ma-0 pa-0" three-line>
        <v-list-item class="ma-0 pa-0" >
          <v-row class="ma-0 pa-0">
            <v-col class="pa-0 ma-0" cols="2">{{person.mlevel}}</v-col>
            <v-col class="ma-0 pa-0" cols="4">
                <v-col class="ma-0 pa-0" v-if="person.ranking_changed < 0">
                  <v-row class="ma-0 pa-0">
                    <p class="font-weight-black ma-0">
                      {{ person.ranking }}
                    </p>
                    <v-icon class="red--text pa-0 ma-0">mdi-arrow-down-bold-circle</v-icon>
                    {{ person.ranking_changed }}
                  </v-row>
                </v-col>
                <v-col class="ma-0 pa-0" v-if="person.ranking_changed == 0">
                  <v-row class="ma-0 pa-0">
                    <p class="font-weight-black ma-0">
                      {{ person.ranking }}
                    </p>
                    <div class="ml-3">-</div>
                  </v-row>
                </v-col>
                <v-col class="ma-0 pa-0" v-if="person.ranking_changed > 0">
                  <v-row class="ma-0">
                    <p class="font-weight-black pa-0 ma-0">
                      {{ person.ranking }}
                    </p>
                    <v-icon class="green--text">mdi-arrow-up-bold-circle</v-icon>
                    {{ person.ranking_changed }}
                  </v-row>
                </v-col>
            </v-col>
            <v-col class="ma-0 pa-0" cols="4">{{ person.member_id }}</v-col>
            <v-col class="ma-0 pa-0" cols="2">{{ person.total_ordered_price }}</v-col>
          </v-row>
        </v-list-item>
      </v-list>
    </v-container>
    <!-- 기본적으로 버튼과 버튼 사이에 마진이 들어감 -->
  </v-card>
</template>

<script>
import apiRanking from "@/apis/ranking";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "ranking",
  // 추가하고 싶은 컴포넌트를 등록
  components: {},
  //컴포넌트에서 사용하는 데이터를 정의
  // return 값이 되려면 괄호로 감싸야함
  data() {
    return {
      check : true,
      people: [],
      myranking :""
    };
  },
  //컴포넌트에서 사용하는 메소드 정의
  methods: {
    getRanking(from, to) {
      if (from === "undefined" || to==="undefined") {
        from = 0;
        to = 10;
      }
      apiRanking.getRanking(from, to)
        .then((response) => {
          console.log(response.data);
          this.people = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getMyRanking() {
      this.check = true;
      apiRanking.getMyRanking()
      .then((response) => {
        this.myranking = response.data;
        let from = Math.floor(response.data/10)*10;
        let to = from+10;
        console.log(from+" "+to);
        this.getRanking(from, to);
      });
    },
  },
  created() {
    console.log("created");
    var from = this.$route.query.from;
    var to = this.$route.query.to;
    this.getRanking(from, to);
  },
};
</script>
<!-- 컴포넌트 -->
<style scoped></style>
